import { useEffect,
         useState,
         useRef }          from "react"

import { useSelector,
         useDispatch,
         shallowEqual, }     from 'react-redux'

import { useForm,
         useWatch }          from "react-hook-form"
import { navigate }          from 'hookrouter'

import "./BookingEditor.css"


import {
    _Date,
    TIME_SLOTS,
    fetchAPI,
}                              from 'JS/services.mjs'

import {
    store,
    APP_CONSTANTS,
    SELECTORS,
    getUser,
    getFlyersData,
}                              from 'index'

import { API_CANCEL_BOOKING,
         API_CHANGE_BOOKING,
         API_ADD_BOOKING }     from 'JS/request.mjs'

import {
    getFlyersFilter,
    findFlyerData,
    Autocomplete,
 }                             from '../FlyersEditor'


import { toggleSidebarMD,
         sidebar_curObjUPD,
         newFlyerNameUPD, }    from "JS/store"

import { useRendersCount }     from 'JS/customHooks'

const DEFAULT_BOOKING_VALS = {
    bookingId: '',
    history:   '',
    time:      0,
    timeVal:   0,
    tarif:     'ПР',
    fullname:  '',
    notes:     '',
    status:    '...',
    flyerId:   0,
    email:     '',
    phone:     '',
}

const MAIN_WATCHED_FIELDS = ['bookingId', 'flyerId', 'time', 'timeVal', 'tarif', 'status', 'notes']


const PAYMENT_ENUM = [
    '...',
    'Серт.',
    'Инвойс',
    'Резерв',
    'Оплачено',
]

const TARIF_MAP = {
      1: 'Серт',
     10: 'ПР_1:30',
     20: 'ПР -20%',
     30: 'ПР',
     40: 'Школа',
     50: 'Промо',
     60: 'Бл-ть',
     70: 'Стафф',
    100: 'Спорт',
    150: 'Sport',
    200: 'Other',
}

function GET_DEFAULTS() {
    const resultObj = {
        ...DEFAULT_BOOKING_VALS,
        time: Date.now(),
        submitOptions: {},
        sumbitChoice: ''
    }
    return resultObj
}

function parseDateTime(dateTime) {
    const RecDate     = new Date(dateTime || Date.now())
    const TimeSlotN   = _Date.msToSlotN(dateTime)

    const Flight_Date = RecDate.toISOString().substring(0, 10)
    const TimeSlot    = TIME_SLOTS[TimeSlotN]?.[2]

    return {Flight_Date, TimeSlot}
}
function codeDateTime(date, slot) {
    return Date.parse(`${date} ${slot} GMT`)
}


function handleWatch({values, initialValues, setValue}) {
    const changedValues = MAIN_WATCHED_FIELDS.reduce(
        (acc, field, index) => (
            values[index] === initialValues[field] ?
            acc :
            {...acc, [field]:values[index] }
        )
        , {}
    )
    const isChanged   = !!Object.entries(changedValues).length
    let submitOptions = isChanged ?
                        { toChange: !changedValues.time,
                            toMove:!!changedValues.time,
                            toNew:   true}
                        : {toCancel: true}
    setValue('submitOptions', submitOptions )

    if (!isChanged) return

    setValue('sumbitChanges', changedValues )

    //debugger
     console.log(changedValues)
}


function onSubmit(e) {
     const {sumbitChoice} = e
     //debugger
      console.log('SUBMIT', e)
     e.sumbitChoice = ''
     store.dispatch(sidebar_curObjUPD(e))

     if (sumbitChoice === 'toCancel') {
        cancelBooking(e.bookingId)
     }

     if (sumbitChoice === 'toChange' || sumbitChoice === 'toMove') {
        changeBooking(e)
     }

     if (sumbitChoice === 'addBooking') {
        addBooking(e)
     }
}

function cancelBooking(bookingId) {
    API_CANCEL_BOOKING({bookingId: bookingId})
}

function changeBooking(bookingObj) {
    let { bookingId,
          sumbitChanges } = bookingObj

    if ( 'flyerId' in sumbitChanges) {
        const {email, fullname, phone} = bookingObj
        sumbitChanges = {...sumbitChanges, email, fullname, phone}
    }

    if ( 'time' in sumbitChanges) {
        const dateTimeStr = `${bookingObj.Flight_Date} ${bookingObj.TimeSlot}`
        sumbitChanges = {...sumbitChanges, dateTimeStr}
    }

    const changes = Array.from(Object.entries(sumbitChanges))

     console.log('CHANGE: ', bookingId, changes)
    API_CHANGE_BOOKING({ bookingId, changes })
}

function addBooking(bookingObj) {
    const reqFieldsMap = APP_CONSTANTS.bookingsAPIDataMap
    const dateTimeStr = `${bookingObj.Flight_Date} ${bookingObj.TimeSlot}`


    const bookingDataArr = []
    Object.entries(reqFieldsMap).forEach(
        ([fieldName, fieldIndex]) => {
            bookingDataArr[ fieldIndex] = bookingObj[fieldName.replace('CI','')]
        }
    )

    bookingDataArr[ reqFieldsMap.timeCI ] = dateTimeStr

     console.log('ADD: ', JSON.stringify(bookingDataArr))
    API_ADD_BOOKING({bookingData: bookingDataArr})
}



function closeSidebar() {
    store.dispatch(toggleSidebarMD(false))
    store.dispatch(sidebar_curObjUPD(GET_DEFAULTS()))
}

function onKeyDown(e) {
    if (e.key === 'Escape') {
        closeSidebar()
    }
}



function BookingEditor() {
    const renderCnt = useRendersCount()
    const dispatch        = useDispatch()
    const initialValues   = useSelector(SELECTORS.sidebar_curObj) || GET_DEFAULTS()
    const flyersData      = useSelector(SELECTORS.flyersDataRaw)

    const { register,
            control,
            setValue,
            getValues,
            watch,
            handleSubmit,
            formState, }  = useForm({defaultValues: {
                                ...initialValues,
                                ...parseDateTime(initialValues.time),
                                   submitOptions: {},
                                   sumbitChanges: {},
                                   sumbitChoice: ''
                                }
                            })


    const [flyerInput,
           setFlyerInput] = useState({ flyerId:  initialValues.flyerId,
                                       fullname: initialValues.fullname })


    useEffect( () => {
        const flyerId = flyerInput.flyerId
        if (flyerId === 'NEW') {
            dispatch( newFlyerNameUPD(flyerInput.fullname) )
            return
        }

        const flyerData = findFlyerData({flyerId, flyersData})
        flyerData.forEach(
            field => setValue(field[0], field[1])
        )
    }, [dispatch, setValue, flyersData, flyerInput])


    const [Flight_Date, TimeSlot] = watch(['Flight_Date', 'TimeSlot'])
    useEffect(() => {
        if ( Flight_Date === initialValues['Flight_Date']
          && TimeSlot    === initialValues['TimeSlot'] ) return

        setValue( 'time', codeDateTime(Flight_Date, TimeSlot) )
    }, [Flight_Date, TimeSlot, initialValues, setValue])


    const watchedMainValues = watch(MAIN_WATCHED_FIELDS)
    useEffect(() => {
        handleWatch({values: watchedMainValues, initialValues, setValue})
    }, [watchedMainValues, initialValues, setValue])




    return (
        <div id="sidebar"
            className = "sidebar"
            onKeyDown = {onKeyDown}
            tabIndex  = "0"
        >
            <button id="closeSidebarBtn" onClick={closeSidebar}>X</button>
            <header className="sidebar-header">
                <h3>Редактор бронирования</h3>
            </header>



            <form
                noValidate
                className = "side-form"
                onSubmit  = {handleSubmit(onSubmit)}
            >
                <div className="side-form__inlineWrapper">
                    <label>
                        ID Брони:
                        <input {...register("bookingId", { required: true })}
                                   disabled
                                   type        = "number"
                                   placeholder = "NEW_ID"
                       />
                    </label>
                </div>



                <label>Дата и время полёта</label>
                <div className="side-form__inlineWrapper">
                    <input  {...register("Flight_Date", { required: true })}
                                type        = "date"
                                placeholder = "Flight_Date"
                    />

                    <select {...register("TimeSlot", { required: true })}
                                className = 'timeslot'
                    >
                        {
                            TIME_SLOTS.map( (slot, i) => (
                                <option key={i} value={slot[2]}>{slot[2]}</option>
                            ))
                        }
                    </select>
                </div>



                <div className="side-form__inlineWrapper">
                    <label>
                        Мин.
                        <input {...register("timeVal", { required:      true,
                                                         valueAsNumber: true,
                                                         min:           0 } )}
                                   type        = "number"
                                   placeholder = "Minutes"
                        />
                    </label>
                    <label>
                        Тариф
                        <select {...register("tarif", { required: true })}>
                            {
                                Object.entries(TARIF_MAP).map( (tarif) => {
                                    const tarifName = tarif[1]
                                    const tarifId   = tarif[0]
                                    return <option key={tarifId} value={tarifName}>{tarifName}</option>
                                })
                            }
                        </select>
                        Оплата
                        <select {...register("status")}>
                            {
                                PAYMENT_ENUM.map( (option, i) => (
                                    <option key={i} value={option}>{option}</option>
                                ))
                            }
                        </select>
                    </label>
                </div>



                <label htmlFor="fullname">Клиент</label>
                <Autocomplete
                    flyersData    = { flyersData }
                    initValue     = { initialValues.fullname }
                    setFlyerInput = { setFlyerInput }
                />



                <label htmlFor="status">Комментарии</label>
                <input type="text" placeholder="Notes" {...register("notes", { required: false })} />

                <SubmitBlock
                    control   = {control}
                    setValue  = {setValue}
                    getValues = {getValues}
                    renderCnt = {renderCnt}
                />
            </form>
        </div>
    )
}


export default BookingEditor


function SubmitBlock({control, setValue, renderCnt}) {
    const submitOptions = useWatch({
      control,
      name: 'submitOptions',
    })

    const submitButtonsText = {
        toCancel: 'Отменить бронирование',
        toMove:   'Перенести бронирование',
        toChange: 'Изменить бронирование',
        toNew:    'Записать как новое'
    }

    const onClick = e => setValue('sumbitChoice', e.target.name)


    const SubmitButtons = Object.entries(submitOptions).map(
        ([option, isSet]) => isSet && <button
                                    type    = 'submit'
                                    key     = {option}
                                    name    = {option}
                                    onClick = {onClick}
                               >
                                   {submitButtonsText[option]}
                               </button>
    )

    return <div id = "SubmitBlock">
        {/* {renderCnt}<br /> */}
        {SubmitButtons}
    </div>
}