import { useEffect,
         useState }          from "react"
import { useForm }           from "react-hook-form"
import { navigate }          from 'hookrouter'

import "./LoginForm.css"
import LoginInfo             from "./LoginInfo"

import {
    fetchAPI,
}                            from 'JS/services.mjs'

import {
    store,
    APP_CONSTANTS,
    SELECTORS,
    getUser,
}                            from 'index'

import {
    userDataUPD,
}                            from 'JS/store.js'



function storeUser({name, mail, ps, role='user'}) {
    localStorage.setItem('name', name)
    localStorage.setItem('mail', mail)
    localStorage.setItem('ps', ps)
    localStorage.setItem('role', role)
    store.dispatch( userDataUPD( {name, mail, ps, role,} ) )
  }




function checkApiResponse(apiResponse) {
    let clearedApiResponse = {
        status: false,
        content: ''
    };

    //debugger
     console.log(apiResponse)

    if (!apiResponse) {
        clearedApiResponse.content = 'No response!';

    } else if (!apiResponse.status === undefined || !apiResponse.content) {
        clearedApiResponse.content = `Invalid response: \n ${apiResponse}`;

    } else if ( /200/.test(apiResponse.status) ) {
        clearedApiResponse.status  = true;
        clearedApiResponse.content = apiResponse.content;
    } else if ( /20\d/.test(apiResponse.status) ) {
        clearedApiResponse.status  = apiResponse.status;
        clearedApiResponse.content = apiResponse.content;
    } else {
        clearedApiResponse.content = `Error!: \n ${apiResponse.content}`;
    }
    return clearedApiResponse;
}

function loginResponseHandler(ReqResult) {
    let userObj          = {}
    let loginResponse    = {}

    let clearedResponse  = checkApiResponse(ReqResult);

    if ( clearedResponse.status === true ) {
        userObj.name     = clearedResponse.content.nick
        userObj.mail     = clearedResponse.content.mail
        userObj.ps       = clearedResponse.content.ps
        userObj.role     = clearedResponse.content.role

        loginResponse    = {
            status:  'SUCSESS',
            message:`Пользователь ${userObj.name}
                           успешно авторизован!`,
            action: () => storeUser(userObj)
        }
    } else if ( clearedResponse.status == 201 ) {
        loginResponse    = {
            status:  'PENDING',
            message: clearedResponse.content,
            action: () => void(0)
        }

    } else {
        loginResponse    = {
            status:  false,
            message: clearedResponse.content,
            action: () => void(0)
        }
    }

    return loginResponse
}// end loginResponseHandler

async function loginFetch (data) {
    const url        = APP_CONSTANTS.COPYGAS_TEMP_BS_URL
    const reqCommand = 'login'

    let reqData  = {
        mail: data.email,
        ps:   data.password,
    }

    let params   = {
                command: reqCommand,
                data: JSON.stringify(reqData)
            }
    let options  = {'method': 'GET'}
    let response = await fetchAPI(url, {params, options})

    return response
}


function getSubmit(setMessage) {
    return async (data) => {
        let loginResponse   = await loginFetch(data)
        let responseHandler = loginResponseHandler(JSON.parse(loginResponse) )

        responseHandler?.action()
        let postAction = (responseHandler?.status == 'SUCSESS') ?
            () => navigate('/default') : null

        setMessage(responseHandler?.message)
        setTimeout(() => {
            setMessage(null)
            postAction?.()
        }, 5000);
    }
}



function LoginForm() {
    const {
        register,
        setValue,
        handleSubmit,
        formState,    } = useForm()

    const [message, setMessage] = useState(null)

    const {
        errors,
        touchedFields,
        isSubmitting, } = formState


    function restorePass(e) {
        //debugger
         console.log('RESTOREPASS: ')
         setValue('password', 'RESTOREPASS')
         handleSubmit()
         setTimeout(() => {
            setValue('password', '')
         }, 100)

    }

    return (
        <div className="login-form">
            <form
                noValidate
                onSubmit = { handleSubmit(getSubmit(setMessage)) }
            >
                <label htmlFor="inputEmail">E-mail</label>
                <input
                    type="email"
                    id="inputEmail"
                    {...register('email', {
                        required: "Enter your e-mail",
                        pattern: {
                            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                            message: "Enter a valid e-mail address"
                        }
                    })}
                />
                { errors.email && <p className="error">{errors.email.message}</p> }

                <label htmlFor="inputPassword">Password</label>
                <input
                    type="password"
                    id="inputPassword"
                    {...register('password', { required: "Enter your password" })} />
                    {errors.password && <p className="error">{errors.password.message}</p>}

                <div id="loginForm-btn-block">
                    <button
                        type     = "submit"
                        disabled = {isSubmitting}
                    >
                        Login
                    </button>
                    <button
                        id       = "restorePass"
                        tabIndex = "-1"
                        onClick  = {restorePass}
                        disabled = {!touchedFields.email || errors.email}
                    >
                        Напомнить
                    </button>

                </div>
            </form>
            {isSubmitting && <LoginInfo message="SUBMITTING ..."/> }
            {message      && <LoginInfo message={message} action={setMessage}/> }
        </div>
    );
}

export default LoginForm