function LoginInfo({status, message, action}) {
    const closeInfo = (e) => {
        action()
    }

    let className = 'loginInfoBtn'
    if (message.includes('SUBMITTING')) className += ' hidden'
    if (message.includes('Error')) className += ' error'


    return (
        <div
            id="loginInfo"
        >
            {message}
            <br /><br />
            <button
                onClick   ={closeInfo}
                className = {className}
            >
                OK
            </button>
        </div>
    )
}

export default LoginInfo
