import { useEffect,
         useState }          from "react"

import { useSelector,
         useDispatch,
         shallowEqual, }     from 'react-redux'

import { useForm,
         useWatch, }         from "react-hook-form"

import { navigate }          from 'hookrouter'

import "./FlyersEditor.css"


import {
    _Date,
    TIME_SLOTS,
    fetchAPI,
}                                 from 'JS/services.mjs'

import {
    store,
    APP_CONSTANTS,
    SELECTORS,
    getUser,
    getFlyersData,
}                                 from 'index'

import { API_ADD_FLYER }          from 'JS/request.mjs'


import { toggleShowflyersDataMD,
         newFlyerNameUPD,
         newFlyerDataUPD, }        from "JS/store"

import Autocomplete               from "./Autocomplete"

const API_FLYERS_MAP = APP_CONSTANTS.flyersAPI_dataMap

const FORM_FIELDS    = ['flyerId', 'fullname', 'lastname', 'firstname', 'secondname', 'email', 'phone', 'catid', 'notes']

const DEFAULT_FLYERS_VALS = {
    flyerId: '',
    fullname: '',
    lastname: '',
    firstname: '',
    secondname: '',
    email: '',
    phone: '',
    catid: 0,
    notes: '',
}

const CAT_ID_MAP = {
    'Х.З.':    0,
    ПР:        1,
    Спорт:     2,
    Дети:      3,
    Сотрудник: 5,
    Дилер:     6,
    'Иностр.': 9,
}
function getCatIdOptions() {
    const jsxArr = []
    for (const category in CAT_ID_MAP) {
        const catId = CAT_ID_MAP[category]
        jsxArr.push(
            <option
               key   = {catId}
               value = {catId}
            >
                {category}
            </option>
        )
    }
    return jsxArr
}
const сatIdOptionsArr = getCatIdOptions()


function setupNewFlyer(flyerInput, setValue) {
    setValue('flyerId', flyerInput.flyerId)
    let fieldsToSet    = ['lastname', 'firstname', 'secondname']
    let flyerInputData = flyerInput.fullname.split(' ')
    fieldsToSet.forEach(
        (field, index) => {
            const value = flyerInputData[ index ]
            if (value) {
                setValue(field, value)
            }
        }
    )

}

function setupExistingFlyer(flyersData, flyerInput, setValue) {
    const fieldsSet = ['flyerId', 'catid', 'firstname', 'secondname', 'lastname', 'email', 'phone', 'notes']
    const choosenFlyerData = flyersData.find(
        flyerRec => flyerRec[API_FLYERS_MAP['flyerId']] === flyerInput.flyerId
    )
    for (const field of fieldsSet) {
        const value = choosenFlyerData[ API_FLYERS_MAP[field] ]
        setValue(field, value)
    }
}

function addFlyer(flyerObj) {
    const reqFieldsMap = APP_CONSTANTS.flyersAPI_dataMap
    flyerObj.fullname  = `${flyerObj.lastname} ${flyerObj.firstname}`
                         + (flyerObj.secondname ? ` ${flyerObj.secondname}` : '')


    const flyerDataArr = []
    Object.entries(reqFieldsMap).forEach(
        ([fieldName, fieldIndex]) => {
            flyerDataArr[ fieldIndex] = flyerObj[fieldName]
        }
    )

     console.log('ADD_FLYER: ', JSON.stringify(flyerDataArr))
     return API_ADD_FLYER({flyerData: flyerDataArr})
}


function getSubmit() {
    return async (flyerObj) => {
        let addFlyerResponse = await addFlyer(flyerObj)
        let newFlyerRecord   = addFlyerResponse.data.record
        //debugger
        console.log('addFlyerResp: ', newFlyerRecord)
        store.dispatch(newFlyerDataUPD(newFlyerRecord))
        setTimeout( closeFlyersEditor )
    }
}

function containerClick(e) {
    if(e?.target?.nodeName ==="HEADER") {
        closeFlyersEditor()
    }
}

function closeFlyersEditor() {
    store.dispatch(toggleShowflyersDataMD(false))
}






export function FlyersEditor() {
    const { register,
            setValue,
            handleSubmit,
            reset, }     = useForm({ defaultValues: DEFAULT_FLYERS_VALS })

    const dispatch       = useDispatch()
    const flyersData     = useSelector(SELECTORS.flyersDataRaw)
    const newNameCall    = useSelector(SELECTORS.flyersData_newName) || false


    let [flyerInput, setFlyerInput] = useState({flyerId:null, fullname:null})

    useEffect( () => {
        if (!newNameCall) return
        setupNewFlyer({flyerId: 'NEW', fullname: newNameCall}, setValue)

        return () => { dispatch(newFlyerNameUPD('')) }
    }, [dispatch, setValue, newNameCall])

    useEffect( () => {
        if (!flyerInput.flyerId) return
        reset()

        if (flyerInput.flyerId === 'NEW') {
            setupNewFlyer(flyerInput, setValue)
        } else {
            setupExistingFlyer(flyersData, flyerInput, setValue)
        }
    }, [flyerInput, flyersData, setValue, reset])



    return (
        <div id="flyersContainer"
            className = "flyersContainer"
            onClick   = {containerClick}
        >
            <header className="flyersForm-header">
                Данные клиента
            </header>

            <form
                noValidate
                id        = "flyersForm"
                className = "flyersForm"
                onSubmit  = {handleSubmit(getSubmit())}
            >

                <input type="text" placeholder="Id" disabled {...register("flyerId", {required: true})} />

                <label htmlFor="autocomplete">Клиент</label>
                <Autocomplete
                    {...register("autocomplete")}
                    flyersData    = { flyersData }
                    setFlyerInput = { setFlyerInput }
                    initValue     = { newNameCall }
                />
                    <input type="text" placeholder="Фамилия"  {...register("lastname", {})} />
                    <input type="text" placeholder="Имя"      {...register("firstname", {})} />
                    <input type="text" placeholder="Отчество" {...register("secondname", {})} />

                <input type="email" placeholder="email" {...register("email", {})} />
                <input type="tel"   placeholder="phone" {...register("phone", {})} />
                <select placeholder="категория клиента" {...register("catid", {})} >
                        {сatIdOptionsArr}
                </select>

                <label htmlFor="status">Комментарии</label>
                <input type="text" placeholder="Notes" {...register("notes", {})} />
                <br /><br />

                <input type="submit" />
            </form>
        </div>
    )
}



export default FlyersEditor
