import React, { Component, Fragment } from "react"

import { getFlyersFilter }            from "../serv"
// import PropTypes from "prop-types";




function SuggestionsListComponent({filteredSuggestions, activeSuggestion, onClick, onMouseOver, onMouseOut}) {
    if (!filteredSuggestions.length) {
        return (
            <div className="no-suggestions" >
                <em>Совпадения не найдены</em>
            </div>
        )
    }

    return (
        <ul className="suggestions">
            {filteredSuggestions.map((suggestion, index) => {
                let className
                let visualisation = suggestion[1]

                if (index === activeSuggestion) {
                    className     = "suggestion-active"
                    visualisation = <>
                        {suggestion[1]}<br />
                        {suggestion[2]}<br />
                        {suggestion[3]}
                    </>
                }

                return (
                    <li key = {suggestion[0]}
                        className    = {className}
                        onClick      = {onClick}
                        onMouseOver  = {onMouseOver}
                        onMouseOut   = {onMouseOut}

                        data-sindex  = {index}
                        data-id      = {suggestion[0]}
                        data-name    = {suggestion[1]}
                    >
                        {visualisation}
                    </li>
                )
            })}
        </ul>
    )
}



// flyersData,
// setFlyerInput,
// initValue

export class Autocomplete extends Component {

    constructor(props) {
        super(props)

        this.state = {
            activeSuggestion: 0,
            filteredSuggestions: [],
            showSuggestions: false,
            userInput: this.props.initValue ||""
        }
    }

    onChoose = ({ foundId, value }) => {
        const { setFlyerInput } = this.props
        value = value.replace('NEW: ', '')

        this.setState({
            activeSuggestion: 0,
            showSuggestions: false,
            filteredSuggestions: [],
            userInput: value
        })

        setFlyerInput({ flyerId: foundId, fullname: value })
        return value
    }

    onFocus = e => {
        e.target.select()
    }

    onChange = e => {
        const flyersFilter       = getFlyersFilter({ flyersData: this.props.flyersData,
                                                     limit: 5})
        const userInput          = e.currentTarget.value
        const newFlyerSuggestion = ['NEW',
                                    'NEW: ' + userInput,
                                    '', '']

        const filteredSuggestions = [newFlyerSuggestion, ...flyersFilter(userInput)]

        this.setState({
            filteredSuggestions,
            activeSuggestion: 0,
            showSuggestions:  true,
            userInput:        e.currentTarget.value
        })
    };


    onClick = e => {
        let foundId = Number(e.target.dataset.id) || 'NEW'
        let value   = e.target.dataset.name
        this.onChoose({ foundId, value })
    }

    onKeyDown = e => {
        const { activeSuggestion, filteredSuggestions } = this.state
        if (e.keyCode === 13) {
            e.preventDefault()
            let choice  = filteredSuggestions[activeSuggestion]
            if(!choice) return
            let foundId = Number(choice[0]) || 'NEW'
            let value   = choice[1]
            this.onChoose({ foundId, value })
        }
        // User pressed the up arrow
        else if (e.keyCode === 38) {
            if (activeSuggestion === 0) return
            this.setState({ activeSuggestion: activeSuggestion - 1 })
        }
        // User pressed the down arrow
        else if (e.keyCode === 40) {
            if (activeSuggestion - 1 === filteredSuggestions.length) {
                return
            }
            this.setState({ activeSuggestion: activeSuggestion + 1 })
        }
    }


    onMouseOver = e => {
        const suggestionIndex = e.target.dataset.sindex
        e.target.className += "suggestion-active"
        this.setState({ activeSuggestion: +suggestionIndex })
    }
    onMouseOut = e => {
        e.target.className = e.target.className.replace("suggestion-active", '')
    }



    // RENDER --------------------------------------------
    render() {
        const {
            onChange,
            onClick,
            onKeyDown,
            onMouseOver,
            onMouseOut,
            onFocus,
            state: {
                activeSuggestion,
                filteredSuggestions,
                showSuggestions,
                userInput
            }
        } = this

        return <Fragment>
            <input
                type       = "text"
                spellCheck = {false}
                onChange   = {onChange}
                onKeyDown  = {onKeyDown}
                value      = {userInput}
                onFocus    = {onFocus}
            />
            {
                (showSuggestions && userInput) &&
                <SuggestionsListComponent
                    filteredSuggestions = {filteredSuggestions}
                    activeSuggestion    = {activeSuggestion}
                    onClick             = {onClick}
                    onMouseOver         = {onMouseOver}
                    onMouseOut          = {onMouseOut}
                />
            }
        </Fragment>
    }
}

export default Autocomplete

